@use "vars" as *;


.c-fc{ //font-color
	&--white{
		color: white !important;
	}
	&--black{
		color: black !important;
	}
	&--red{
		color: #ED2649 !important;
	}
	&--grey{
		color: $grey !important;
	}
}



.c-ft{//font-family
	&--en{
		font-family: $font-en;
	}
	&--jp{
		font-family: $font-jp;
	}
	&--serif{
		font-family: $font-Serif;
	}
}







.c-al{
	&--c{
		text-align: center !important;
	}
	
	&--r{
		text-align: right !important
	}
	
	&--l{
		text-align: left !important;
	}
}



// Image position control
.c-pull{
	&--left{
		margin-left: calc(((100vw - 100%) / 2) * -1)
	}
	&--right{
		margin-right: calc(((100vw - 100%) / 2) * -1)
	}
}










/* ====================================================
Component
==================================================== */
// Common Text style
.c-txt{
	&__basic{

	}
}




//ttl
.c-ttl{
	&__01{
		display: block;
		text-align: center;
		line-height: 1.4;
		font-family: $font-en;
		color: $blue;
		@include font-size(40);
		font-weight: bold;
		margin-bottom: 50px;
		@include mq(){
			@include font-size(24);
			margin-bottom: 30px;
		}
	}
	&__02{
		display: block;
		@include font-size(18);
		font-weight: bold;
		margin-bottom: 30px;
		span{
			display: inline-block;
			position: relative;
			padding: 0 25px 0 0;
			&:after{
				content: "";
				border: solid #0f1a2a;
				border-width: 0 2px 2px 0;
				display: inline-block;
				padding: 4px;
				transform: rotate(-45deg);
				position: absolute;
				top: 9px;
				right: 0;
			}
		}
	}
	&__03{
		display: block;
		@include font-size(24);
		font-weight: bold;
		line-height: 1.4;
		margin-bottom: 20px;
		@include mq(){
			@include font-size(16);
		}
	}
	&__04{
		display: block;
		@include font-size(16);
		text-align: center;
		font-weight: bold;
	}
}

//btn
.c-btn{
	&__01{
		@include flex(center,center,wrap);
		width: 100%;
		max-width: 132px;
		height: 55px;
		border-radius: 55px;
		@include font-size(14);
		color: white;
		font-weight: bold;
		background: rgb(55,85,243);
		background: linear-gradient(90deg, rgba(55,85,243,1) 0%, rgba(19,127,217,1) 100%);
		@include mq(){
			height: 40px;
			max-width: 92px;
		}
		span{
			display: block;
			position: relative;
			padding: 0 36px 0 0;
			&:after{
				content: "";
				width: 24px;
				height: 24px;
				background: url("../img/arrow-right.png")no-repeat;
				background-size: 100%;
				position: absolute;
				@include v-align;
				right: 0;
			}
		}
	}
	&__02{
		@include flex(center,center,wrap);
		text-align: center;
		@include font-size(16);
		font-weight: 600;
		color: white;
		background: rgb(55,85,243);
		background: linear-gradient(90deg, rgba(55,85,243,1) 0%, rgba(19,127,217,1) 100%);
		width: 100%;
		max-width: 130px;
		height: 48px;
		border-radius: 50px;
		border: none;
		cursor: pointer;
		@include mq(){
			width: 48px;
			border-radius: 50%;
		}
		img{
			display: block;
			width: 20px;
			margin: 0 5px 0 0;
		}
		span{
			@include mq(){
				display: none;
			}
		}
		&--gray{
			background: #E2E8F0;
			color: #1E2A3B;
		}
	}
	&__03{
		@include flex(center,center);
		@include font-size(14);
		font-weight: bold;
		background: rgb(55,85,243);
		background: linear-gradient(90deg, rgba(55,85,243,1) 0%, rgba(19,127,217,1) 100%);
		width: 110px;
		height: 44px;
		border-radius: 50px;
		text-align: center;
		border: none;
		outline: none;
		padding: 0 35px;
		cursor: pointer;
		@include smooth-transition;
		color: white;
		&:hover{
			opacity: 0.7;
		}
		&--gray{
			background: #E2E8F0;
			color: #1E2A3B;
			width: 150px;
		}
	}
	&__04{
		@include flex(center,center,wrap);
		text-align: center;
		width: 180px;
		height: 48px;
		border-radius: 50px;
		border: none;
		cursor: pointer;
		outline: none;
		@include font-size(16);
		background: rgb(55,85,243);
		background: linear-gradient(90deg, rgba(55,85,243,1) 0%, rgba(19,127,217,1) 100%);
		font-weight: bold;
		color: white;
		position: relative;
		@include smooth-transition;
		margin: 0 auto;
		&:after{
			content: "";
			width: 24px;
			height: 24px;
			background: url("../img/success.png")no-repeat;
			background-size: 100%;
			position: absolute;
			@include v-align;
			right: 10px;
		}
		&:hover{
			opacity: 0.7;
		}

	}
}


//fade up
.js-scrollin {
	opacity: 0;
	-webkit-transform: translate(0, 70px);
	-ms-transform: translate(0, 70px);
	transform: translate(0, 70px);
	&.active{
		opacity: 1;
		-webkit-transform: translate(0, 0);
		-ms-transform: translate(0, 0);
		transform: translate(0, 0);
		-webkit-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
		-o-transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
		transition: 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
	}
}



.tooltip-hv{
	position: relative;
	z-index: 99;
	&:hover{
		opacity: 1;
		.tooltip-box{
			opacity: 1;
			visibility: visible;
		}

	}

}
.tooltip-box{
	background: white;
	border-radius: 16px;
	@include font-size(14);
	font-weight: 400;
	color: #0F1A2A;
	padding: 15px;
	@include smooth-transition;
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: calc(100% + 15px);
	z-index: 9;
	left: calc(50%);
	width: 270px;
	text-align: left;
	border: 1px solid #E2E8F0;
	&__ttl{
		display: block;
		font-weight: bold;
		@include font-size(16);
		margin-bottom: 10px;
	}
}